import { useEffect, useState } from "react";
import Sketch from "react-p5";
import p5Types from "p5";
import SystemUtil from "../../utils/systemUtil";
import SetterFrame from "./setterFrame";
import FormUtil from "../../utils/form/formUtiil";

namespace PlayerFrame {

    const CANVAS_WIDTH = SystemUtil.CONTENTS_WIDTH;
    type XY = {
        x: number;
        y: number;
    }
    class Vec {
        v: XY;
        len: number;
        rad: number;
        constructor(dirRad: number, len: number) {
            this.v = {
                x: Math.cos(dirRad) * len,
                y: Math.sin(dirRad) * len
            };
            this.rad = dirRad;
            this.len = len;
        }
        syncVecToRad() {
            this.rad = Math.atan2(this.v.y, this.v.x);
            this.len = Math.sqrt(Math.abs(this.v.x) * Math.abs(this.v.x) + Math.abs(this.v.y) * Math.abs(this.v.y));
        }
    }

    const getRGBA = (color: { r: number, g: number, b: number, a: number }) => {
        const r = ('00' + color.r.toString(16)).slice(-2);
        const g = ('00' + color.g.toString(16)).slice(-2);
        const b = ('00' + color.b.toString(16)).slice(-2);
        const a = ('00' + color.a.toString(16)).slice(-2);
        return `#${r}${g}${b}${a}`;
    }

    class Color {
        r: number;
        g: number;
        b: number;
        a: number;

        constructor(r: number, g: number, b: number) {
            this.r = r;
            this.g = g;
            this.b = b;
            this.a = 255;
        }

        getRGBA() {
            return getRGBA({ r: this.r, g: this.g, b: this.b, a: this.a });
        }
    }
    class Particle {

        pos: XY;
        prevPos: XY;
        vec: Vec;
        radius: number;

        color: Color;
        timer: number;

        isLive: boolean;

        histories: XY[];

        extend: Extend;

        constructor(pos: XY, vec: Vec, radius: number, extend: Extend) {
            this.pos = this.prevPos = pos;
            this.vec = vec;
            this.radius = radius;
            this.color = new Color(255, 255, 255);
            this.extend = extend;

            this.timer = 0;
            this.isLive = true;
            this.histories = [];
        }
    }

    type Extend = {
        modulate: (self: Particle) => void;
        draw: (self: Particle) => void;
        props: any;
    }

    type MainBarrProp = {
        limit: number;
        startColor: Color;
        endColor: Color;
    }

    type EmbersProp = {
        limit: number;
        color: Color;
    }
    type SeedProp = {
        color: Color;
        shadeNum: number;
    }

    export const Component = (props: {
        req: SetterFrame.Request;
        back: () => void;
    }) => {
        const [ls, setLs] = useState<{
            time: number;
            list: Particle[];
            nextUnitIndex: number;
        }>({
            time: 0,
            list: [],
            nextUnitIndex: -1
        });
        const invalidate = () => setLs({ ...ls });

        // const updateNextTime = () => {
        //     const nextUnit = props.req.units.find(u => u.time > ls.nextTime);
        //     if (nextUnit != undefined) {
        //         ls.nextTime = nextUnit.time;
        //         invalidate();
        //     }
        // }

        useEffect(() => {
            if (props.req.units.length > 0) {
                ls.nextUnitIndex = 0;
                invalidate();
            }
        }, []);

        const setup = (p5: p5Types, canvasParentRef: Element) => {
            p5.createCanvas(CANVAS_WIDTH, CANVAS_WIDTH).parent(canvasParentRef);
            p5.background('#000');
            p5.frameRate(60);
        };

        const draw = (p5: p5Types) => {
            p5.background('#000000');

            if (ls.nextUnitIndex === -1) return;
            const unit = props.req.units[ls.nextUnitIndex];

            const createSeed = () => {

                unit.seeds.forEach((seed, i) => {
                    const interval = CANVAS_WIDTH / (unit.seeds.length + 1);
                    const x = interval + interval * i;

                    const seedExt: SeedProp = i % 2 === 0 ? ({
                        color: new Color(100, 255, 133),
                        shadeNum: 10
                    }) : ({
                        color: new Color(255, 1, 155),
                        shadeNum: 10
                    });
                    ls.list.push(new Particle(
                        { x, y: CANVAS_WIDTH },
                        new Vec(Math.PI * 1.5, 6),
                        13,
                        {
                            modulate: (self) => {
                                const ext: SeedProp = self.extend.props;
                                // 
                                self.vec.v.y += 0.05;

                                // self.histories.unshift({ ...self.pos });
                                // if (self.histories.length > self.extend.props.shadeNum) self.histories.splice(self.histories.length - 1, 1);

                                if (self.timer === 0) {
                                    self.color = ext.color;
                                }

                                if (self.vec.v.y < 0) {
                                    createTracer(self, ext.color);
                                }
                                if (self.vec.v.y > 2) {
                                    self.isLive = false;

                                    createBarrage(seed, self);
                                }
                                // self.histories.unshift({ ...self.pos });
                                // if (self.histories.length > ext.shadeNum) self.histories.splice(self.histories.length - 1, 1);
                            },
                            draw: (self) => {
                                // const limit: number = self.extend.props.limit;
                                // // self.color.a = Math.floor(255 * ((limit - self.timer) / limit));

                                // p5.noStroke();
                                // p5.fill(self.color.getRGBA());
                                // p5.ellipse(self.pos.x, self.pos.y, self.radius, self.radius);

                                // self.histories.forEach(pos => {
                                //     const subA = Math.floor(self.color.a * ((limit - self.timer) / limit));
                                //     p5.fill(getRGBA({ ...self.color, a: subA }));
                                //     p5.ellipse(pos.x, pos.y, self.radius, self.radius);
                                // });
                            },
                            props: seedExt
                        }
                    ));
                });
            }

            const createTracer = (parent: Particle, color: Color) => {
                const parNum = 2;
                const ext: MainBarrProp = {
                    limit: Math.floor(21 + Math.random() * 42),
                    startColor: new Color(255, 110, 110),
                    endColor: new Color(120, 110, 255)
                }
                for (let i = 0; i < parNum; i++) {
                    const rangeRad = 0.12;
                    ls.list.push(new Particle(
                        { x: parent.pos.x, y: parent.pos.y },
                        new Vec(
                            Math.PI / 2 - rangeRad / 2 + Math.random() * rangeRad,
                            (Math.random() * Math.abs(parent.vec.v.y)) * 2
                        ),
                        3 + Math.random() * 5,
                        // new Color(255, Math.floor(Math.random() * 155), 0),
                        {
                            modulate: (self) => {
                                const ext: MainBarrProp = self.extend.props;
                                const limit = ext.limit;
                                if (self.timer === 0) {
                                    self.color = ext.startColor;
                                }
                                if (self.timer === limit) self.isLive = false;


                                if (self.timer === 0) self.color = color;

                                // 1
                                if (self.vec.len > 1) {
                                    self.vec.v.x *= 0.91;
                                    self.vec.v.y *= 0.91;
                                    self.vec.syncVecToRad();
                                }

                            },
                            draw: (self) => {
                                const ext: MainBarrProp = self.extend.props;
                                const limit = ext.limit;

                                // self.color.a = Math.floor(255 * ((limit - self.timer) / limit));
                                const radRate = ((limit - self.timer) / limit);
                                const radius = self.radius * radRate;
                                p5.noStroke();

                                p5.fill(self.color.getRGBA());
                                p5.ellipse(self.pos.x, self.pos.y, radius, radius);
                            },
                            props: ext
                        }
                    ));
                }
            }

            const createBarrage = (seed: SetterFrame.Seed, self: Particle) => {

                seed.pars.forEach((par, i) => {
                    const barrExt: MainBarrProp = {
                        limit: Math.floor(80 + Math.random() * 120),
                        startColor: new Color(255, 11, 1),
                        endColor: new Color(120, 110, 255)
                    };

                    const radian = Math.atan2(par.y, par.x);
                    const lenRate = 0.08;
                    const len = Math.sqrt(Math.abs(par.x) * Math.abs(par.x) + Math.abs(par.y) * Math.abs(par.y)) * lenRate;
                    ls.list.push(new Particle(
                        { x: self.pos.x, y: self.pos.y },
                        new Vec(
                            radian,
                            len
                        ),
                        5 + Math.random() * 8,
                        {
                            modulate: (self) => {
                                const ext: MainBarrProp = self.extend.props;
                                const limit: number = ext.limit;

                                const rSub = ext.endColor.r - ext.startColor.r;
                                const gSub = ext.endColor.g - ext.startColor.g;
                                const bSub = ext.endColor.b - ext.startColor.b;
                                const limitRate = self.timer / limit;
                                self.color.r = ext.startColor.r + Math.floor(rSub * limitRate);
                                self.color.g = ext.startColor.g + Math.floor(gSub * limitRate);
                                self.color.b = ext.startColor.b + Math.floor(bSub * limitRate);

                                if (self.timer === limit) {
                                    self.isLive = false;

                                    createEmbers(self, ext.endColor);
                                }

                                // self.histories.unshift({ ...self.pos });
                                // if (self.histories.length > self.extend.props.shadeNum) self.histories.splice(self.histories.length - 1, 1);

                                // 1
                                if (self.vec.len > 0.4) {
                                    self.vec.v.x *= 0.91;
                                    self.vec.v.y *= 0.91;
                                    self.vec.syncVecToRad();
                                    // console.log(`len: ${self.vec.len}`);
                                }
                            },
                            draw: (self) => {
                                const limit: number = self.extend.props.limit;
                                let rate = 1;
                                const start = 30;
                                if (limit - self.timer < start) {
                                    rate = (limit - self.timer) / start;
                                }
                                // self.color.a = Math.floor(255 * rate);
                                p5.noStroke();

                                // self.histories.forEach(pos => {
                                //     const subA = Math.floor(self.color.a * ((limit - self.timer) / limit));
                                //     p5.fill(getRGBA({ ...self.color, a: subA }));
                                //     p5.ellipse(pos.x, pos.y, self.radius, self.radius);
                                // });
                                const color = self.color;
                                p5.stroke(color.getRGBA());
                                // color.a = 255 * rate;
                                // color.g = Math.random() * 100;
                                // p5.drawingContext.shadowBlur = 20;
                                // p5.drawingContext.shadowColor = '#ff0000e1';
                                p5.fill(color.getRGBA());
                                const radius = self.radius * rate;
                                // const radius = Math.floor(self.radius * (-0.7 + Math.random() * 0.6));
                                p5.ellipse(self.pos.x, self.pos.y, radius, radius);
                                // p5.ellipse(self.pos.x, self.pos.y, self.radius, self.radius);
                            },
                            props: barrExt
                        }
                    ));

                    createDust(self, barrExt.startColor, radian, len);
                });
            }

            /**
             * 
             * @param parent 
             */
            const createEmbers = (parent: Particle, color: Color) => {

                ls.list.push(new Particle(
                    { x: parent.pos.x, y: parent.pos.y },
                    new Vec(
                        Math.PI / 2,
                        0.5
                    ),
                    4,
                    {
                        modulate: (self) => {

                            self.vec.v.y += 0.006;
                            const limit: number = self.extend.props.limit;
                            if (self.timer === 0) {
                                self.color = color;
                            }
                            if (self.timer === limit) {
                                self.isLive = false;
                            }
                        },
                        draw: (self) => {
                            if (self.timer % 12 >= 6) return;
                            self.color.a = 160;
                            // p5.drawingContext.shadowColor = '#fff';
                            // p5.drawingContext.shadowBlur = 50;
                            p5.fill(self.color.getRGBA());
                            p5.ellipse(self.pos.x, self.pos.y, self.radius, self.radius);
                        },
                        props: {
                            limit: Math.floor(20 + Math.random() * 40),
                        }
                    }
                ));
            }

            /**
             * 
             * @param parent 
             * @param color 
             * @param radian 
             * @param len 
             */
            const createDust = (parent: Particle, color: Color, radian: number, len: number) => {
                const randNum = Math.floor(Math.random() * 3);
                for (let j = 0; j < randNum; j++) {
                    const secondRate = Math.random() * 2.3;
                    ls.list.push(new Particle(
                        { x: parent.pos.x, y: parent.pos.y },
                        new Vec(
                            radian,
                            len * secondRate
                        ),
                        2 + Math.random() * 3,
                        // new Color(255, Math.floor(Math.random() * 155), 0),
                        {
                            modulate: (self) => {
                                const limit: number = self.extend.props.limit;
                                if (self.timer === limit) self.isLive = false;

                                if (self.timer === 0) self.color = color;

                                // 1
                                if (self.vec.len > 1) {
                                    self.vec.v.x *= 0.91;
                                    self.vec.v.y *= 0.91;
                                    self.vec.syncVecToRad();
                                }

                            },
                            draw: (self) => {
                                const limit: number = self.extend.props.limit;
                                self.color.a = Math.floor(255 * ((limit - self.timer) / limit));
                                p5.noStroke();

                                p5.fill(self.color.getRGBA());
                                p5.ellipse(self.pos.x, self.pos.y, self.radius, self.radius);
                            },
                            props: {
                                limit: Math.floor(30 + Math.random() * 77),
                                shadeNum: 0
                            }
                        }
                    ));
                }
            }

            if (ls.time === unit.time) {

                createSeed();

                if (props.req.units.length > ls.nextUnitIndex + 1) {
                    ls.nextUnitIndex++;
                    invalidate();
                }
            }

            for (let i = ls.list.length - 1; i >= 0; i--) {
                const par = ls.list[i];

                p5.noStroke();

                par.pos.x += par.vec.v.x;
                par.pos.y += par.vec.v.y;

                par.extend.modulate(par);

                par.extend.draw(par);

                par.timer++;
                if (!par.isLive) {
                    ls.list.splice(i, 1);
                }
            }
            ls.time++;
            // invalidate();

            p5.textSize(20);
            p5.fill('#f5cdcd')
            p5.text(`Frame: ${ls.time}`, 5, 20);
            p5.text(`Second: ${Math.floor(ls.time / 60)}`, 5, 40);
            p5.fill('#d8f5cd')
            p5.text(`Particle: ${ls.list.length}`, 5, 60);
        };

        return (<>
            <Sketch setup={setup} draw={draw} />
            <FormUtil.ButtonRecord textAlign="left" buttons={[
                {
                    label: 'Restart', width: 130, callback: () => {
                        ls.time = 0;
                        ls.nextUnitIndex = 0;
                        ls.list.length = 0;
                        invalidate();
                    }
                },
                {
                    label: 'Back', width: 130, callback: () => {
                        props.back();
                    }
                }
            ]} />
        </>);
    }
}

export default PlayerFrame;